import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex);

//Add the Axios call here

//We need filteredItems in the store...So Single Cat is oulling data from the same

const store = new Vuex.Store({
	state: {
		items: [],
		itemsLength: 0,
		errored: "",
		loading: true
	},
	mutations: {
		getItems(state) {
			axios
				.get("https://api.thecatapi.com/v1/breeds", {
					headers: {
						"x-api-key": "1d8f6fd8-cfe3-415c-a77d-752841c617f1"
					}
				})
				.then(response => {
					const data = response.data;

					for (let item of data) {
							state.items.push({
							name: item.name,
							description: item.description,
							id : item.id,
							temperament: item.temperament,
							url: item.wikipedia_url
						});
					}

					state.itemsLength = state.items.length;
				})
				.catch(error => {
					console.log(error);
					state.errored = true;
				})
				.finally(() => (state.loading = false));
		}
	},
	actions: {
		getItems(items) {
			items.commit("getItems");
		}
		/*
		,
		filteredItems(items) {
			items.commit("filteredItems");
		}
		*/
	}
});

store.dispatch("getItems");
//store.dispatch("filteredItems");

export default store;
