<template>
  <div class="cat-single">
    <div>
      <div class="back-home">
        <router-link to="/">&#8249; Back Home</router-link>
      </div>
      <b-card
        no-body
        style="max-width: 40rem; margin: 2rem auto;"
        img-alt="Image"
        img-top
      >

	  <p v-if="!imageLoaded">Loading Image</p>
	  <img class="text-center" :src="itemImage" style="max-height: 20rem; height: 100%; width: 100%; object-fit: contain;"/>
        <template v-slot:header>
          <h4 class="mb-0">{{ item.name }}</h4>
        </template>

        <b-card-body>
          <b-card-text>
            <div>
              <p>
                <strong>Description:</strong>
              </p>
              <p>{{ item.description }}</p>
            </div>
            <div>
              <p>
                <strong>Temperament:</strong>
              </p>
              <p>{{ item.temperament }}</p>
            </div>
          </b-card-text>
        </b-card-body>

        <b-card-footer>
          <a :href="item.url" target="__blank" class="card-link text-center">More information on the {{ item.name }}</a>
        </b-card-footer>
      </b-card>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";

export default {
  data() {
    return {
	  item: "",
	  imageLoaded: false,
    itemImage: ""
    };
  },
  computed: {
    ...mapState(["items"])
  },
  methods: {
    getItemImage: function( id ) {
      //const itemId = this.item.id;
      axios
        .get(`https://api.thecatapi.com/v1/images/search?breed_ids=${id}`, {
          headers: {
            "x-api-key": "1d8f6fd8-cfe3-415c-a77d-752841c617f1"
          }
        })
        .then(response => {
          const data = response.data;
		      this.itemImage = data[0].url;
		      this.imageLoaded = true;
        })
        .catch(error => {
          console.log(error);
          this.$store.state.errored = true;
        })
        .finally(() => (this.$store.state.loading = false));
    }
  },
  mounted() {

    console.log(this.$route.params.name);
    /*
    (this.item = this.items[this.$route.params.name]),
    this.getItemImage(this.item.id);
    */
   const vm = this;

    this.item = this.items.find(function(element) {
       return element.name === vm.$route.params.name;
    });

    this.getItemImage(this.item.id);
    
  }
};
</script>

<style lang="scss">

  .back-home {
    margin-top: 2rem;

    a {
      color: #05386b;
    font-weight: bold !important;
    }
  }

  .card {
    min-height: 40rem;
  }

  h4 {
    color: #05386b;
    font-weight: bold !important;
  }

  img {
    margin: 1rem auto;
  }

  .card-text {

    div {
      margin-bottom: 2rem;
    }
  }
</style>