<template>
      <div class="home">
        <div>
      <b-jumbotron border-variant="dark" style="background-color: #8ee4af;">
          <template v-slot:header>Cat Breeds</template>

          <div style="transform: rotate(10deg); text-align: center;">
            <font-awesome-icon icon="paw" style="font-size: 5rem; margin-right: 1rem;"/>
          </div>
  

        <template v-slot:lead>
          A simple Vue.js App powered by the <a href="thecatapi.com" target="_blank">thecatapi</a>, coded by <a href="sergioca.com" target="blank">Sergi Oca</a> as a study on Vue.js, Vuex, and Vue Router.
        </template>

        <hr class="my-4">

        <p>
          Use the search box below to find a breed or just browse the list to find some beautiful cats.
        </p>
      </b-jumbotron>
    </div>
     <div class="search-wrapper">
        <b-form-input type="text" v-model="search" placeholder="Search Breed.."></b-form-input>
      </div>
    <b-container class="mt-3 mb-5">
      <b-table
        hover
        :fields="fields"
        id="my-table"
        :items="filteredItems"
        :per-page="perPage"
        :current-page="currentPage"
        style="min-height: 30rem;"
      >
        <template v-slot:cell(name)="data">
          <router-link :to="`/cat/${data.value}`" style="color: #05386b; font-weight: bold;">{{ data.value }}</router-link>
        </template>
      </b-table>

      <b-pagination 
        class="mt-5"
        v-model="currentPage"
        :total-rows="filteredItems.length"
        :per-page="perPage"
        aria-controls="my-table"
        align="center"
      ></b-pagination>
    </b-container>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      fields: ['name', 'temperament'],
      loading: true,
      errored: false,
      perPage: 12,
      currentPage: 1,
      search: ''
      
      //items: []
    };
  },
  computed: {
    ...mapState(["items", "itemsLength"]),
    filteredItems() {
      return this.items.filter(item => {
         return item.name.toLowerCase().indexOf(this.search.toLowerCase()) > -1
      })
    }
  }
}
</script>

<style lang="scss">
  .pagination {
    margin: 0 auto;
  }

  .jumbotron {
    background-color: #8ee4af;
    color: #f8ffec;
    margin-bottom: 0 !important;
  }

  .search-wrapper {
    background-color: rgba(0, 0, 0, 0.075);
    width: 100%;
    height: 5rem;
    display: flex;
    align-items: center;
    margin-bottom: 3rem;

    input {
      max-width: 30rem;
      margin: 0 auto;
    }
  }
</style>